body {
    font-family: Helvetica,Arial,sans-serif;
    background-color: rgba(0, 0, 0, 0);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 0;
}
#fullscreen {
    /** Fullscreen Background image:
    https://css-tricks.com/perfect-full-page-background-image/#aa-css-only-technique-1
     */
    top: 0;
    left: 0;
    min-height: 100%;
    min-width: 1024px;
    width: 100%;
    height: auto;
    position: fixed;
    display: block;
    z-index: -999;
    /*opacity: 0.8;*/
}
#avatar {
    height: 180px;
    border-radius: 100%;
    border: 3px solid white;
    margin-bottom: 40px;
}
h1 {
    color: white;
    font-weight: normal;
    letter-spacing: 0.1em;
    font-size: 4em;
    font-family: Helvetica,Arial,sans-serif;
}
h3 {
    padding: 30px;
    color: white;
    font-weight: bold;
}
.content {
    padding-top: 50px;
    background-color: rgba(0, 0, 0, 0);
    text-align: center;
}
ul {
    list-style: none;
    padding: 0;
    text-align: center;
}
li {
    border-radius: 100%;
    border: 2px transparent;
    color: white;
    margin: 8px;
    display: inline-block;
    list-style: none;
    padding: 10px;
}
li:hover {
    color: white;
    text-decoration: none;
    -webkit-transition: 500ms;
    -moz-transition: 500ms;
    transition: 500ms;
    background-color: rgba(0, 0, 0, 0.25);
}
a {
    color: white;
    text-decoration: none;
}

.social-link > * {
    vertical-align: middle;
    margin:0;
}
.social-icon{
    display: none;
}
.social-network {
    display: inline-block;
    color: white;
    font-family: Helvetica,Arial,sans-serif;
    font-weight: normal;
}

@media (max-width: 450px) {
    ul {
        text-align: center;
    }

    li {
        border-radius: 0;
        border: none;
        color: white;
        display: block;
        list-style: none;
        margin: 10px 0;
        text-align: center;
        height: auto;
        width: auto;
    }

    li:hover {
        background-color: transparent;
        transition: none;
    }

    a {
        text-align: justify;
    }

    a:hover {
        -webkit-transition: 500ms;
        -moz-transition: 500ms;
        transition: 500ms;
        background-color: rgba(0, 0, 0, 0.5);
        color: white;
        text-decoration: none;
    }

    .social-icon {
        display: none;
    }
    .social-network {
        display: inline-block;
    }
}
